import Topbar from '../../components/topBar/topbarx.vue'
export default {
	data() {
		return {
				title: "我的地址",
				addressList: [], // 地址列表
		}
	},
	onShow() {
	this.getData()	
	},
	methods: {
		// 添加地址
		goAddress(){
			uni.navigateTo({
				url:'../addressadd/addressadd'
			})
		},
		childbar(val) {
			switch (val.identify) {
				case "leftHandle":
					break;
				case "rightHandle":
					this.$router.push({
						path: "/pwdreg",
						query: {
							tag: "reg",
							btn: "reg"
						}
					});
					break;
			}
		},
		// 返回上级目录
		goBack(){
			uni.switchTab({
				url: '../mine/mine'
			})
		},
		// 请求数据
		getData(){
			let _this = this
			let userinfo = this.$store.state.userInfo
			console.log('userinfo', userinfo)
			uni.showLoading({
				title: '加载中...'
			})
			uni.request({
				url: this.$http + '/api/user/userAddress',
				method: 'POST',
				data: {
					token: userinfo.token
				},
				success(res) {
					console.log('我的地址列表数据', res)
					uni.hideLoading()
					if(res.data.code == 1){
						if(res.data.data.length == 0){
							uni.showModal({
								title: '提示',
								content: '暂无数据'
							})
						}
						_this.addressList = res.data.data
					}
				}
			})
		},
		goEdit(item){
			const { province,city,area,addres,name,phone,id} = item
			uni.navigateTo({
				url:'../addressadd/editaddress?province=' + province + '&city=' + city + '&area=' + area + '&addres=' + addres + '&name=' + name + '&phone=' + phone + '&id=' + id
			})
		}
	},
	components: {
		Topbar
	}
}
